.btn-rounded {
  position: relative;
  padding: 0.9375rem 4.375rem 0.9375rem 1.25rem;
  font-weight: 700;
  margin: 0;
  border: 0.1rem solid rgba(255, 255, 255, 0.6);
  background-color: black;
}
@media (max-width: 575.98px) {
  .btn-rounded {
    display: none;
  }
}
.btn-rounded span {
  position: absolute;
  background-color: #9926f0;
  width: 2.5rem;
  height: 2.5rem;
  top: 50%;
  right: 5px;
  transform: translateY(-50%);
  border-radius: 50%;
}
.btn-rounded span i {
  color: #fff;
  font-size: 1rem;
  line-height: 2.5rem;
}
.btn-rounded:hover {
  background: linear-gradient(to right, rgba(153, 38, 240, 0.95), rgba(209, 34, 227, 0.95));
  color: #fff;
}
.btn-rounded:hover span {
  background-color: rgba(0, 0, 0, 0.6);
}

.display-2 {
  margin-bottom: 1rem;
  text-transform: capitalize;
}
.display-2--intro {
  display: inline-block;
  font-weight: 700;
}
.display-2--description {
  font-size: 1rem;
  display: block;
  margin-top: 1.2rem;
  text-transform: none;
}
@media (min-width: 992px) {
  .display-2--description {
    font-size: 1.4rem;
    display: inline-block;
  }
}

.heading-line::before {
  content: "";
  width: 10rem;
  height: 0.1rem;
  display: block;
  margin: 0 auto;
  background-color: #9926f0;
}

.heading-line::after {
  content: "";
  width: 2rem;
  padding-top: 0.5rem;
  height: 0.2rem;
  display: block;
  margin: 0 auto;
  margin-bottom: 1rem;
  background-color: #9926f0;
}

body {
  font-family: "Inria Serif", serif;
}

h1, .h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  font-family: "Quicksand", sans-serif;
}

/** Sections SCSS Folder */
.menu {
  background: linear-gradient(to right, rgba(153, 38, 240, 0.95), rgba(209, 34, 227, 0.95));
}
.menu li {
  padding: 0 0.7rem;
}
.menu li a {
  color: #fff;
  text-transform: capitalize;
  font-weight: 600;
}

.intro-section {
  background: linear-gradient(to right, rgba(153, 38, 240, 0.95), rgba(209, 34, 227, 0.95));
  padding: 10rem 0 0 0;
  width: 100%;
  height: 100%;
}

.intros .video-box {
  position: relative;
}
.intros .video-box a span i {
  color: #d122e3;
}

.border-animation {
  position: absolute;
  top: 0;
  left: 0;
  width: 6rem;
  height: 6rem;
  border: 0.55rem solid #fff;
  border-radius: 50%;
  animation: pulse-border 1.5s linear infinite;
}
.border-animation--border-1 {
  animation-delay: 1s;
}
.border-animation--border-2 {
  animation-delay: 1.5s;
}

.companies {
  padding: 0 0 5rem 0;
}
.companies__logo-box {
  max-width: 100%;
  height: 3rem;
  margin-bottom: 2rem;
  padding: 0.5rem;
  text-align: center;
  position: relative;
}
.companies__logo-box img {
  height: 100%;
}
.companies__logo-box img:hover {
  filter: grayscale(120);
}

/** Footer */
.footer {
  background: linear-gradient(to right, rgba(153, 38, 240, 0.95), rgba(209, 34, 227, 0.95));
  color: #fff;
  border: 2px solid #fff;
}

