ntawesome';\n@import '~@fortawesome/fontawesome-free/scss/solid';\n@import '~@fortawesome/fontawesome-free/scss/brands';","@use '../custom' as *;\n\n/** Google Fonts */\n@import url('https://fonts.googleapis.com/css2?family=Inria+Serif&family=Quicksand:wght@500&display=swap');\n\nbody {\n    font-family: 'Inria Serif', serif;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n    font-family: 'Quicksand', sans-serif;\n}","// PULSE BORDER\n@keyframes pulse-border {\n    0% {\n        transform: scale(1);\n        opacity: 1;\n    }\n\n    100% {\n        transform: scale(1.5);\n        opacity: 0;\n    }\n}","/** Bootstrap Functions and Variables */\n//@import '~bootstrap/scss/functions';\n//@import '~bootstrap/scss/variables';\n\n// MY THEME COLORS\n$purple-dark: #9926f0;\n$purple-light: #BB6EF5;\n$pink: #d122e3;\n\n// WHITE AND GRAY COLORS\n$white: #fff;\n$gray-100: #f8f9fa;\n$gray-200: #e9ecef;\n$gray-300: #dee2e6;\n$gray-400: #ced4da;\n$gray-500: #adb5bd;\n$gray-600: #6c757d;\n$gray-700: #495057;\n$gray-800: #343a40;\n$gray-900: #212529;\n$black: #000;\n\n$primary: $purple-dark;\n$secondary: $pink;\n\n// // merge with existing $theme-colors map\n// $theme-colors: map-merge($theme-colors, (\"primary\": $primary,\n//             \"secondary\": $secondary, \"success\": $success));\n\n// Accordion Overrides\n$accordion-icon-color: $primary;\n$accordion-icon-active-color: $secondary;\n$accordion-icon-width: 1.875rem;\n\n$accordion-button-icon: url(\"data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='currentColor' class='bi bi-plus-circle' viewBox='0 0 16 16'><path d='M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z' /> <path d='M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4z' /></svg>\");\n$accordion-button-active-icon: url(\"data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='currentColor' class='bi bi-dash-circle' viewBox='0 0 16 16'>